import React from 'react';
import { graphql } from 'gatsby';
import DocsLayout from '../layouts/docs/docs-layout';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import SEO from 'components/seo';

const DocsTemplate = ({ data }) => {
  const { mdx } = data;
  const { body, frontmatter } = mdx;

  return (
    <DocsLayout>
      <SEO title={mdx.frontmatter.title} />
      <div className="DocsArticle">
        <div className="DocsArticle-heading">
          <h2 className="DocsArticle-title">{frontmatter.title}</h2>
          <h5 className="DocsArticle-subtitle">{frontmatter.description}</h5>
        </div>
        <div className="DocsArticle-content">
          <MDXRenderer>{body}</MDXRenderer>
        </div>
      </div>
    </DocsLayout>
  );
};

export const query = graphql`
  query DocsByPath($id: String) {
    mdx(id: { eq: $id }) {
      frontmatter {
        path
        title
        description
      }
      body
    }
  }
`;

export default DocsTemplate;
